.detailView {
	/* width: 100%; */
	margin: 0;
	width: calc(100% - 400px);
	float: left;
}

.detailViewTopBar {
	display: none !important
}

@media only screen and (max-width: 960px) {
	.detailView {
		width: 100%;
		float: none;
	}

	.detailViewTopBar {
		display: -ms-flexbox !important;
		display: flex !important
	}	
}


